  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-row :gutter="10">
      <el-col :xs="20" :sm="16" :md="16" :lg="14">
        <!--  -->
        <el-form ref="user-form" :model="user" label-width="80px" :rules="userRules">
          <el-form-item label="">
            {{ user.id }}
          </el-form-item>
          <el-form-item label="">
            {{ user.mobile }}
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="user.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="intro">
            <el-input type="textarea" v-model="user.intro" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="user.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmitSettings" :loading="loading"></el-button>
          </el-form-item>
        </el-form>
        <!-- / -->
      </el-col>
       <el-col :xs="4" :sm="8" :md="8" :lg="10">
        <label for="file" class="head-file">
          <el-image
          class="head"
          :src="user.photo"
          fit="fit"/>
          <p></p>
        </label>
        <!--
          :
          : 
          1.pinputfile,labelinputid,
          2.,inputchange(), onFileChange ,,,input, inputDOM,value this.$refs.file.value = '' 
          3.,,(,):
            a: :this.dialogVisible = true,  append-to-body
            b: : ,,:src=previewImage,
            const file = this.$refs.file.files[0]
            const blobData = window.URl.createObjectURL(file) url src
            this.previewImage = bobData previewImageurlsrc ,

            : 
            1.cropperjs,
            2.,
            3.css,
            4.,,img ,,  @opened="onDialogOpened"
            5.,,,
            6.:  destroy  .replace(url)
            7.cropper,data,openedthis.cropper,closedthis.cropper.destroy  opened if (this.cropper) {
              this.cropper.relace(this.previewImage)
              return
            }
            8.
            9., cropper() this.cropper.getCroppedCanvas().toBlob(file => {
              const fd = new FormData()
              fd.append('photo', file)  
              (fd).then(res => {
                this.dialogVisible = false  
                this.user.photo = window.URL.createObjectURL(file)  ,
                
                this.user.photo = res.data.data.photo 
              })
            })

            ,,,inputchange
            1.DOM const file = this.$refs.file
            2.(file) const fileObj = file.files[0]
            3.FormData
            const fd = new FormData()
            ,
            const blob = window.URL.createObjectURL(fileObj)
            blobsrc
            this.previewImage = blob(vue)
            document.querySelector('#img).src = blob (js)
        -->
        <!-- change  -->
        <input
          ref="file"
          type="file"
          id="file"
          hidden
          @change="onFileChange"
        >
      </el-col>
    </el-row>
  </el-card>
  <!-- , -->
  <!-- @opened="onDialogOpened" , -->
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    append-to-body
    @opened="onDialogOpened"
    @closed="onDialogClosed"
  >
    <!--  -->
    <div class="preview-image-wrap">
      <!--  -->
      <img
        :src="previewImage"
        class="preview-image"
        ref="preview-image"
      >
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="onUpdatePhoto" :loading="onPhotoLoading"> </el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
import { getUserProfile, edituserProfile, editUserPhoto } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'
export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        id: null,
        mobile: '',
        name: '',
        intro: '',
        email: ''
      },
      // 
      userRules: {
        name: [
          { required: true, message: '', tigger: 'blur' },
          { min: 1, max: 7, message: '17', tigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', tigger: 'blur' },
          {
            // 
            validator (rule, valid, callback) {
              if (!valid) {
                callback(new Error(''))
              }
              callback()
            }
          }
        ],
        email: [
          { required: true, message: '', tigger: 'blur' },
          { pattern: /^\w+@\w+(\.\w+)+$/, message: '', tigger: 'blur' }
        ]
      },
      loading: false, // 
      dialogVisible: false, // 
      previewImage: '',
      cropper: null, // 
      onPhotoLoading: false // loading
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUserProfile()
  },
  mounted () {},
  methods: {
    // 
    loadUserProfile () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    // 
    onSubmitSettings () {
      this.loading = true
      // 
      this.$refs['user-form'].validate(async valid => {
        if (!valid) {
          // 
          return this.$message('')
        }
        // 
        try {
          const res = await edituserProfile(this.user)
          this.$message({
            type: 'success',
            message: ''
          })
          this.loading = false
          // 
          globalBus.$emit('update-user', this.user)
        } catch (err) {
          this.$message({
            type: 'danger',
            message: ''
          })
          this.loading = false
        }
      })
    },
    // ,,
    onFileChange () {
      editUserPhoto().then(res => {
        // ,,FormData
        // 
        const file = this.$refs.file

        // urlsrc
        const bolbData = window.URL.createObjectURL(file.files[0])

        this.previewImage = bolbData

        // 
        this.dialogVisible = true

        // input change
        this.$refs.file.value = ''
      })
    },
    // dialog
    onDialogOpened () {
      //  img 
      // : img 
      // ,
      // imageDOM
      const image = this.$refs['preview-image']

      // 1, ,
      // ,
      // : .replace() 
      // : ,
      // 
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return
      }
      this.cropper = new Cropper(image, {
        // 
        aspectRatio: 1,
        // 
        viewMode: 1,
        // 
        dragMode: 'none',
        // 
        cropBoxResizable: false

        // 
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    onDialogClosed () {
      // ,
      // this.cropper.destroy()
    },

    // Promise
    // 1.
    getCroppedCanvas () {
      // 2. newPromise
      return new Promise((resolve, reject) => {
        // 3.
        // 4.resolve(),reject
        this.cropper.getCroppedCanvas().toBlob(file => {
          // 
          resolve(file)
        })
      })
    },

    // 
    async onUpdatePhoto () {
      this.onPhotoLoading = true
      const file = await this.getCroppedCanvas()
      const fd = new FormData()
      // 
      fd.append('photo', file)
      // 
      const res = await editUserPhoto(fd)
      // 
      this.dialogVisible = false
      // 
      // blob
      // file
      this.user.photo = window.URL.createObjectURL(file)

      //  
      // this.user.photo = res.data.data.photo
      this.$message({
        type: 'success',
        message: ''
      })
      this.onPhotoLoading = false
      // 
      globalBus.$emit('update-user', this.user)
    }
  }
}
</script>

